.sidenav {
  width: 77px;
  min-height: 240px;

  text-align: right;
}


.sidenav__list {
  @include reset-list;
}


// .sidenav__item {
// }


.sidenav__link {
  position: relative;

  display: block;
  padding-top: 15px;
  padding-bottom: 15px;

  color: $color--white-50;


  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: -10px;

    display: block;
    box-sizing: content-box;
    width: 3px;
    height: 100%;
    padding-left: 7px;

    background-clip: content-box;
    background-color: $color--white-50;
  }

  &:hover {
    color: $color--white;
    background-image: linear-gradient(to right, transparent, $color--white-10);
    background-repeat: no-repeat;
    background-position-x: 30px;
    transition: background 0.7s, color 0.5s;


    &::after {
      background-color: $color--white;
    }
  }


  &--current {
    color: $color--white-80;

    &::after {
      background-color: $color--white-80;
    }
  }

}

.sidenav__text {
  &--short {
    display: none;
  }
}

@media (max-width: $desktop-width) {
  .sidenav {
    width: 35px;
  }

  .sidenav__text {
    display: none;

    &--short {
      display: block;
    }
  }
}

