.pretitle {
  @include reset;

  position: relative;

  padding-left: 104px;

  font-family: $font-family;
  font-size: 1.13rem;
  // font-size: calc(var(--index) * 0.62);
  line-height: 1.22;
  font-weight: 800;
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 6px;
  color: $color--accent;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;

    width: 72px;
    height: 2px;
    background-color: $color-bg--accent;
  }
}


@media (max-width: $tablet-width) {
  .pretitle {
    padding-left: 70px;

    font-size: 1.13rem;
    letter-spacing: 4px;


    &::before {
      width: 50px;
    }
  }
}
