    }
  }

  .no-webp .parallax__wrapper {
    background-image: url("../img/bg-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-tablet@2x.jpg");
    }
  }
}
