  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 1.13rem;
  font-weight: 700;
  line-height: 1.78;
  color: #ffffff;
  background-color: #0B1D26;
  counter-reset: guide;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

