  max-width: 1660px;
  margin: 0 auto;
  padding: 0 110px;
}
.container--header {
  max-width: 1840px;
  padding: 0 2vw;
}

@media (max-width: 1200px) {
  .container {
    padding: 0 75px;
  }
  .container--header {
    padding: 0 2vw;
  }
}
