  }
  .container--header {
    padding: 0 2vw;
  }
}
.visually-hidden {
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(100%); /* stylelint-disable-line */
  clip-path: inset(100%);
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  padding: 0;
  border: 0;
  position: absolute !important; /* stylelint-disable-line */
}

.content {
  will-change: transform;
}

.parallax {
  position: relative;
}
.parallax::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  right: 0;
  z-index: 3;
  width: 100%;
  height: 20%;
  background: linear-gradient(180deg, rgba(11, 29, 38, 0) 0%, #0B1D26 61.38%);
}

.parallax__wrapper {
  position: relative;
  z-index: 0;
  display: flex;
  justify-content: center;
  height: 100vh;
  overflow-y: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: 25%;
}

.parallax__layer {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  will-change: transform;
}

.webp .parallax__layer--1 {
  background-image: linear-gradient(330.24deg, rgba(11, 29, 38, 0) 31.06%, #0B1D26 108.93%), url("../img/bg-hg-desktop@1x.webp");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .webp .parallax__layer--1 {
    background-image: linear-gradient(330.24deg, rgba(11, 29, 38, 0) 31.06%, #0B1D26 108.93%), url("../img/bg-hg-desktop@2x.webp");
  }
}

.no-webp .parallax__layer--1 {
  background-image: linear-gradient(330.24deg, rgba(11, 29, 38, 0) 31.06%, #0B1D26 108.93%), url("../img/bg-hg-desktop@1x.png");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .no-webp .parallax__layer--1 {
    background-image: linear-gradient(330.24deg, rgba(11, 29, 38, 0) 31.06%, #0B1D26 108.93%), url("../img/bg-hg-desktop@2x.png");
  }
}

.parallax__layer--2 {
  top: 45%;
}

.webp .parallax__layer--2 {
  background-image: url("../img/bg-mg-desktop@1x.webp");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .webp .parallax__layer--2 {
    background-image: url("../img/bg-mg-desktop@2x.webp");
  }
}

.no-webp .parallax__layer--2 {
  background-image: url("../img/bg-mg-desktop@1x.png");
}
