.pretitle {
  margin: 0;
  padding: 0;
  position: relative;
  padding-left: 104px;
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 1.13rem;
  line-height: 1.22;
  font-weight: 800;
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 6px;
  color: #FBD784;
}
.pretitle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 72px;
  height: 2px;
  background-color: #FBD784;
}

@media (max-width: 660px) {
  .pretitle {
    padding-left: 70px;
    font-size: 1.13rem;
    letter-spacing: 4px;
  }
  .pretitle::before {
    width: 50px;
  }
}
.title {
  margin: 0;
  padding: 0;
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-size: calc(var(--index) * 2.05);
  line-height: 1.2;
  font-weight: 600;
  text-align: left;
}

.link {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.13rem;
  line-height: 1.22;
  text-transform: lowercase;
  color: #FBD784;
  cursor: pointer;
}
.link svg {
  transform: rotate(270deg);
}
.link:hover {
  animation: arrow-right 300ms ease-in-out alternate infinite;
}
.link:hover svg {
  transform: rotate(630deg);
  transition: transform 200ms ease-in-out;
}
.link--scroll {
  color: #ffffff;
}
.link--scroll svg {
  transform: none;
}
.link--scroll:hover {
  animation: arrow-bottom 300ms ease-in-out alternate infinite;
}
.link--scroll:hover svg {
  transform: rotate(360deg);
  transition: transform 200ms ease-in-out;
}

.link__text {
  padding-right: 16px;
}

@keyframes arrow-right {
  from {
    transform: translate(0);
  }
  to {
    transform: translate(0.25rem);
  }
}
@keyframes arrow-bottom {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(0.25rem);
  }
}
.main-nav {
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.main-nav__link {
  position: relative;
  display: block;
  padding: 10px 30px;
  color: inherit;
}
.main-nav__link::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 50%;
  display: block;
  width: 0;
  height: 2px;
  background-color: #FBD784;
  transition: background 0.5s, width 0.5s, left 0.5s;
}
.main-nav__link:hover {
  color: #FBD784;
  transition: color 0.5s;
}
.main-nav__link:hover::after {
  left: 0;
  width: 100%;
  transition: width 0.5s, left 0.5s;
}

@media (max-width: 960px) {
  .main-nav {
    display: none;
  }
  .main-nav--open {
    display: flex;
    width: 100%;
    flex-direction: column;
    animation-name: fadein;
    animation-duration: 0.6s;
  }
  .main-nav--open .main-nav__list {
    justify-content: flex-start;
    flex-direction: column;
  }
  .main-nav--open .account__text {
    display: none;
  }
}
@media (max-width: 660px) {
  .no-js .main-nav__list {
    flex-direction: column;
  }
}
@keyframes fadein {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.social {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 230px;
  color: rgba(255, 255, 255, 0.8);
}
.social:hover {
  color: #ffffff;
}

.social__desc {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  writing-mode: vertical-rl;
  cursor: default;
}

.social__link {
  display: block;
  width: 40px;
  height: 40px;
  color: inherit;
  transition: color 0.5s;
  text-align: center;
}
.social__link:hover svg {
  color: #FBD784;
  transform-origin: 20%;
  transform: rotate(360deg);
  transition: transform 0.5s, color 0.5s;
}

@media (max-width: 660px) {
  .social {
    min-height: auto;
  }
}
.sidenav {
  width: 77px;
  min-height: 240px;
  text-align: right;
}

.sidenav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidenav__link {
  position: relative;
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  color: rgba(255, 255, 255, 0.5);
}
.sidenav__link::after {
  content: "";
  position: absolute;
  top: 0;
  right: -10px;
  display: block;
  box-sizing: content-box;
  width: 3px;
  height: 100%;
  padding-left: 7px;
  background-clip: content-box;
  background-color: rgba(255, 255, 255, 0.5);
}
.sidenav__link:hover {
  color: #ffffff;
  background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.1));
  background-repeat: no-repeat;
  background-position-x: 30px;
  transition: background 0.7s, color 0.5s;
}
.sidenav__link:hover::after {
  background-color: #ffffff;
}
.sidenav__link--current {
  color: rgba(255, 255, 255, 0.8);
}
.sidenav__link--current::after {
  background-color: rgba(255, 255, 255, 0.8);
}

.sidenav__text--short {
  display: none;
}

@media (max-width: 1200px) {
  .sidenav {
    width: 35px;
  }
  .sidenav__text {
    display: none;
  }
  .sidenav__text--short {
    display: block;
  }
}
.page-header {
  min-height: 100vh;
  position: absolute;
  width: 100%;
}

.page-header__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.page-header__nav-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  padding: 10px 0;
  background-color: transparent;
  transition: background 0.5s;
}
.page-header__nav-wrapper--scroll {
  background-color: #000000;
  transition: background 0.6s;
}

.page-header__burger {
  display: none;
}

.page-header__social {
  position: fixed;
  top: 40vh;
  left: 2vw;
  z-index: 4;
}
@media (min-width: 1840px) {
  .page-header__social {
    left: calc((100% - 1840px) / 2 + 2vw);
  }
}

.page-header__sidenav {
  position: fixed;
  top: 40vh;
  right: 2vw;
  z-index: 4;
}
@media (min-width: 1840px) {
  .page-header__sidenav {
    right: calc((100% - 1840px) / 2 + 2vw);
  }
}

@media (max-width: 960px) {
  .page-header__burger {
    box-sizing: content-box;
    position: relative;
    display: block;
    width: 40px;
    height: 2px;
    padding: 20px;
    background-color: #ffffff;
    background-clip: content-box;
    border: none;
    cursor: pointer;
  }
  .page-header__burger::before, .page-header__burger::after {
    content: "";
    position: absolute;
    left: 20px;
    width: 40px;
    height: 2px;
    background-color: #ffffff;
    transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  .page-header__burger::before {
    top: 10px;
